$dark-gray: #ede9e5;
$gold: #ceac65;


#ourteam {
height: 140vh;
    .teamContainer {
        height: 80vh;
        font-family: 'Montserrat', sans-serif;
        line-height: 1.5;
        background-color: $dark-gray;
        width: 100vw;
        display: flex;
        color: $gold;
        box-sizing: border-box;
    }
    .contactSection {
        height: 60vh;
        font-family: 'Montserrat', sans-serif;
        line-height: 1.5;
        width: 100vw;
        display: flex;
        align-items: center;
        justify-content: center;
        color: white;
        box-sizing: border-box;
        background-color: $dark-gray;
        .mapPlace {
            width: 40%;
            height: 100%;
            background-color: white;
            min-width: 300px;
            border-top-right-radius: 25px;
            border-bottom-right-radius: 25px;
            background-image: url('../../public/mapa.JPG');
            background-size: cover;
            background-position: center;
            background-repeat: no-repeat;
        }
        .contactDetails {
            width: 60%;
            display: flex;
            align-items: center;
            justify-content: center;
        }
        .contactContainer {
            width: 60%;
            min-width: 800px;
            height: 40vh;
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: #474747;
            border-radius: 25px;
            span {
                font-size: 13px;
            }
        }
    }

    .businessPhoto {
        width: 100%;
        height: 40vh;
        background-color: white;
        margin-right: 10%;
    }

    .businessPhotofirst {
        width: 95%;
        height: 20vh;
        background-color: white;
        margin-bottom: 20px;
    }
  
    p {
        color: rgb(78, 78, 78);
        margin-bottom: 25px;
    }

    h1 {
        font-size: 30px;
        margin-top: 0;
    }



    .productDesc {
        color: rgb(107, 107, 107);
        margin: 0;
        font-size: 13px;
        
    }

    .leftContainer {
        width: 50%;
        
        height: 100%;
        display: flex;
        justify-content: end;
        margin-right: 5%;
        .aboutUs {
            min-width: 430px;
            width: 50%;
            margin-top: 5%;
        }
    }

    .rightContainer {
        width: 50%;
        height: 100%;
        .aboutUs {
            min-width: 430px;
            width: 50%;
            margin-top: 5%;
        }
    }
}