$dark-gray: #333333;
$gold: #ceac65;
#calculator {
    height: 65vh;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: white;
    font-family: 'Montserrat', sans-serif;
    background-color: #f6f3ee;
    .contactBTN {
      width: 450px;
      border: 3px solid white;
      border-radius: 25px;
      text-align: center;
      padding: 8px;
      cursor: pointer;
      transition: all 0.5s;
      &:hover {
        border: 3px solid $gold;
        color: $gold;
        box-shadow: 0 5px 5px rgba(237, 255, 136, 0.1);
        filter: drop-shadow(0 15px 15px rgba(237, 255, 136, 0.1));
      }
    }
    .descriptionHelp {
      padding: 2%;
      height: 25vh;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      align-items: center;
      background-image: url('../../public/marmur.jpg');
      width: 100%;
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
      p {
        font-size: 35px;
        font-weight: bold;
        margin: 0;
      }
    }
    .description {
        height: 40vh;
        color: black;
        
        min-width: 900px;
        width: 60%;
        padding-left: 5rem;
        padding-right: 5rem;
        line-height: 1.6;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        text-align: justify;
    
        h2 {
          color: $gold;
          font-weight: bold;
          font-size: 2rem;
          margin-bottom: 1rem;
        }
    
        p {
          font-size: 1rem;
          margin-bottom: 1rem;
        }
      }
}
