$dark-gray: #333333;
$gold: #ceac65;
#workWithUS {
    height: 45vh;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: white;
    font-family: 'Montserrat', sans-serif;
    background-color: #f6f3ee;
    
    
   
    .descriptionHelp {
        box-sizing: border-box;
      height: 45vh;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      align-items: center;
      background-image: url('../../public/marmur.jpg');
      width: 100vw;
      background-repeat: no-repeat;
      background-size: cover;
      padding-left: 10%;
      padding-right: 10%;
      background-position: center;

      h2 {
        font-size: 35px;
      }
      span {
        font-size: 12px;
        font-weight: bold;
      }
      p {
        font-size: 13px;
        color:rgb(209, 209, 209);
      }
    }
    .gainContainer {
        display: flex;
        justify-content: space-between;
        align-items: center;
        .gainTile {
          display: flex;
          flex-direction: column;
          align-items: center;
          width: 20%;
          text-align: center;
          height: 100%;
        }
        .gainIcon {
          font-size: 40px;
          margin-bottom: 10px;
        }
    }
        
      
}
