$dark-gray: #ede9e5;
$gold: #ceac65;


#leasingOffer {
    height: 80vh;
    width: 100%;
    background-color: white;
    font-family: 'Montserrat', sans-serif;
    line-height: 1.5;
    color: $gold;
    box-sizing: border-box;

    .creditsOfferContainer {
        padding-left: 10%;
        padding-right: 10%;
        margin-top: 5%;
    }

    .productDesc {
        color: rgb(107, 107, 107);
        margin-bottom: 25px;
    }

    
    h1 {
        font-size: 35px;
        margin-top: 0;
    }

    .creditCardsContainer {
        padding: 0;
    }
    .topRow {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
    }
    .bottomRow {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
    }
    .cardsOffer {
        box-sizing: border-box;
        margin-bottom: 30px;
        width: 20%;
        height: 25vh;
        border-radius: 25px;
        background-color: #ede9e5;
        display: flex;
        align-items: center;
        justify-content: space-around;
        flex-direction: column;
        text-align: center;
        padding: 30px;
        h3 {
            color: rgb(73, 73, 73);
            font-size: 23px;
            height: 50%;
        }
        .iconTopic {
            font-size: 50px;
            
        }
    }
}